@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,300;0,900;1,100&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,300;0,600;0,700;0,800;0,900;1,100;1,800&display=swap);
:root {
  --color-one: 225, 0, 0;
  --background: #000;
  --text-color: 255, 138, 138;
}
* {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  -webkit-tap-highlight-color: transparent;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  -webkit-user-select: none;
          user-select: none;
}
input:focus,
select:focus,
textarea:focus,
button:focus {
  outline: none;
}
body {
  padding: 0;
  margin: 0;
  background: #000;
  background: var(--background);
  color: rgba(255, 138, 138, 0.891);
  color: rgba(var(--text-color), 0.891);
}
.introSub {
  color: rgba(255, 138, 138, 0.891);
  color: rgba(var(--text-color), 0.891);
  font-size: 27px;
}
.introSub strong {
  color: rgb(225, 0, 0);
  color: rgb(var(--color-one));
  border: 1px solid rgb(255 255 255 / 4%);
  padding: 5px 4px;
  margin: 0px 0px;
  border-radius: 5px;
  background: rgb(255 255 255 / 2%);
}
#color {
  bottom: 86px;
  position: fixed;
  left: 6px;
  z-index: 6;
}

.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 74px;
  position: fixed;
  width: 100%;
  margin: auto 0;
  z-index: 5;
  border-bottom: 1px solid rgb(225, 0, 0);
  border-bottom: 1px solid rgb(var(--color-one));
  padding: 0 10vw;
  box-sizing: border-box;
}
.title {
  font-size: 1.4em;
  font-weight: 100;
  letter-spacing: 3vw;
}
.boxes {
  margin: 0 0 0px 0;
  display: grid;
  grid-gap: 19px 10px;
  grid-template-columns: repeat(4, auto);
  grid-template-rows: repeat(3, -webkit-max-content);
  grid-template-rows: repeat(3, max-content);
  justify-content: center;
  align-content: end;
}
.boxStyle div:nth-child(1) {
  padding: 15px 10px 0px 10px;
  margin: 5px;
}
.boxStyle div:nth-child(2) {
  padding: 0px 10px 10px 10px;
  margin: 5px;
}
.boxStyle .boxStyle-item {
  border: 1px solid rgb(225, 0, 0);
  border: 1px solid rgb(var(--color-one));
  padding: 15px;
  margin: 0px;
  font-size: 16px;
  border-left: 0;
  border-right: 0;
  border-radius: 0;
  border-bottom: 0;
}
.boxStyle .boxStyle-item.last {
  border-bottom: 1px solid rgb(225, 0, 0);
  border-bottom: 1px solid rgb(var(--color-one));
}
.backdrop {
  -webkit-backdrop-filter: blur(5px) brightness(150%) saturate(200%) hue-rotate(-30deg);
          backdrop-filter: blur(5px) brightness(150%) saturate(200%) hue-rotate(-30deg);
}
.backdropNav {
  -webkit-backdrop-filter: blur(5px) brightness(50%) saturate(200%) hue-rotate(-30deg);
          backdrop-filter: blur(5px) brightness(50%) saturate(200%) hue-rotate(-30deg);
  background: linear-gradient(
    90deg,
    rgb(0 0 0 / 0%) 25%,
    rgba(225, 0, 0, 0.5) 50%,
    rgb(0 0 0 / 0%) 79%
  );
  background: linear-gradient(
    90deg,
    rgb(0 0 0 / 0%) 25%,
    rgba(var(--color-one), 0.5) 50%,
    rgb(0 0 0 / 0%) 79%
  );
  background-size: 460%;
  -webkit-animation: backdropnav 2.5s cubic-bezier(1, 0, 0.35, 1.08) 0.41s infinite
    normal;
          animation: backdropnav 2.5s cubic-bezier(1, 0, 0.35, 1.08) 0.41s infinite
    normal;
}
@-webkit-keyframes backdropnav {
  0% {
    background-position: -29%;
  }
  100% {
    background-position: -127%;
  }
}
@keyframes backdropnav {
  0% {
    background-position: -29%;
  }
  100% {
    background-position: -127%;
  }
}
.boxStyle {
  color: rgba(255, 138, 138, 0.891);
  color: rgba(var(--text-color), 0.891);
  font-size: 20px;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid rgb(225, 0, 0);
  border: 1px solid rgb(var(--color-one));
  background: rgba(225, 0, 0, 0.1);
  background: rgba(var(--color-one), 0.1);

  transition: opacity 2.6s;
}
.is-inview {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
  -webkit-animation: inview 0.5s cubic-bezier(1, 0, 0.35, 1.08) 0s 1 normal;
          animation: inview 0.5s cubic-bezier(1, 0, 0.35, 1.08) 0s 1 normal;
}
@-webkit-keyframes inview {
  0% {
    scale: 0.3;
  }
  100% {
    scale: 1;
  }
}
@keyframes inview {
  0% {
    scale: 0.3;
  }
  100% {
    scale: 1;
  }
}
.boxes .boxStyle:nth-child(1) {
  grid-column: 1 / 5;
}

.boxes .boxStyle:nth-child(2) {
  grid-column: 1 / 3;
  grid-row: 2 / 4;
}

.boxes .boxStyle:nth-child(3) {
  grid-column: 3 / 5;
}
.boxes .boxStyle:nth-child(4) {
  grid-column: 3 / 5;
}
.div {
  padding: 0px 8vw;
}
.Home {
  font-size: 50px;
}
.btn .icon svg {
  position: absolute;
  height: 78%;
  width: 49%;
  top: 4px;
  right: -5px;
  opacity: 0.43;
}
.navbar {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 80px;
  flex-direction: row;
  flex-wrap: nowrap;
  position: fixed;
  bottom: 0px;
  width: 100%;
  margin: auto 0;
  z-index: 5;
  border-top: 1px solid rgb(225, 0, 0);
  border-top: 1px solid rgb(var(--color-one));
}

.active .btn {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  box-shadow: 1px 1px 0px 0px rgb(225, 0, 0);
  box-shadow: 1px 1px 0px 0px rgb(var(--color-one));
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}

.btn {
  cursor: pointer;
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  color: rgba(255, 138, 138, 0.891);
  color: rgba(var(--text-color), 0.891);
  padding: 11px 33px 11px 15px;
  transition: all 0.08s;
  border-radius: 3px;
  -webkit-user-select: none;
          user-select: none;
  border: none;
  margin: 0px 10px;
  box-shadow: 4px 4px 0 0px rgb(225, 0, 0);
  box-shadow: 4px 4px 0 0px rgb(var(--color-one));
  -webkit-transform: translate(-3px, -3px);
          transform: translate(-3px, -3px);
  border: 1px solid rgb(225, 0, 0);
  border: 1px solid rgb(var(--color-one));
}
.introHead {
  font-family: "Raleway", sans-serif;
  font-weight: 100;
  font-size: 60px;
  margin: 0vh 0 2.6vh 0px;
}
.btnNav {
  padding: 10px 20px;
}
.btn:active {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  box-shadow: 0px 0px 0 0px rgb(225, 0, 0);
  box-shadow: 0px 0px 0 0px rgb(var(--color-one));
  -webkit-transform: translate(0px, 0px);
          transform: translate(0px, 0px);
}
.intro {
  padding: 20px 30px;
}
.boxStyle-head {
  font-size: 30px;
  margin-bottom: 9px;
}
.boxStyle-body {
  font-size: 15px;
}
.boxStyle-icon {
  position: static;
  height: 52px;
}
.boxStyle-icon svg {
  position: absolute;
  bottom: 12px;
  right: 4px;
  height: 29px;
  width: 11vw;
  opacity: 0.71;
  color: rgb(225, 0, 0);
  color: rgb(var(--color-one));
}

.footer-wrap {
  border-top: 1px solid rgb(225, 0, 0);
  border-top: 1px solid rgb(var(--color-one));
  padding: 14px;
}
.footer {
  margin: 10px;
  /* display: grid; */
  grid-gap: 2vw;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-template-rows: repeat(2, 15vh);
  justify-content: center;
  align-content: end;
}
.felem {
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 300px 300px;
  padding: 10px;
  border-top: 1px solid rgb(225, 0, 0);
  border-top: 1px solid rgb(var(--color-one));
}

/* Background begin */

#backWrap {
  position: fixed;
  height: 100vh;
  top: 0;
  left: 0;
  width: 100vw;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-around;
  align-items: center;
  z-index: -1;
}
#backWrap div {
  position: absolute;
  border: 1px solid rgb(225, 0, 0);
  border: 1px solid rgb(var(--color-one));
  border-radius: 0px 0px;
  opacity: 0;
  -webkit-transform: translate(0px, 0px) scale(3);
          transform: translate(0px, 0px) scale(3);
}
#backWrap div:nth-child(1) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 200px;
  width: 300px;
  animation: back 6s cubic-bezier(1, 0, 0.35, 1.08) 0.41s infinite
    alternate-reverse;
}
#backWrap div:nth-child(2) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 200px;
  width: 200px;
  animation: back 4s cubic-bezier(1, 0, 0.35, 1.08) 0.62s infinite
    alternate-reverse;
}
#backWrap div:nth-child(3) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 150px;
  width: 350px;
  animation: back 4s cubic-bezier(1, 0, 0.35, 1.08) 0.83s infinite
    alternate-reverse;
}
#backWrap div:nth-child(4) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 120px;
  width: 120px;
  animation: back 5s cubic-bezier(1, 0, 0.35, 1.08) 1.1s infinite
    alternate-reverse;
}
#backWrap div:nth-child(5) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 50px;
  width: 50px;
  animation: back 1s cubic-bezier(1, 0, 0.35, 1.08) 1.5s infinite
    alternate-reverse;
}
#backWrap div:nth-child(6) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 50px;
  width: 140px;
  animation: back 3s cubic-bezier(1, 0, 0.35, 1.08) 1.6s infinite
    alternate-reverse;
}
#backWrap div:nth-child(7) {
  background-color: rgba(225, 0, 0, 0.1);
  background-color: rgba(var(--color-one), 0.1);
  height: 250px;
  width: 250px;
  animation: back 3s cubic-bezier(1, 0, 0.35, 1.08) 1.3s infinite
    alternate-reverse;
}
@-webkit-keyframes back {
  0% {
    border-radius: 0px 0px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 0px) scale(1);
            transform: translate(0px, 0px) scale(1);
  }
  30% {
    border-radius: 10px 10px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 00px) scale(0.92);
            transform: translate(0px, 00px) scale(0.92);
  }
  40% {
    border-radius: 10px 200px;
    opacity: 0.21;
    -webkit-filter: hue-rotate(-68deg);
            filter: hue-rotate(-68deg);
    -webkit-transform: translate(10px, 10px) scale(1);
            transform: translate(10px, 10px) scale(1);
  }
  60% {
    border-radius: 10px 10px;
    -webkit-transform: translate(5px, -10px) scale(1);
            transform: translate(5px, -10px) scale(1);
    opacity: 0.61;
  }
  70% {
    border-radius: 10px 10px;
    opacity: 0.21;
    -webkit-transform: translate(0px, -00px) scale(0.9);
            transform: translate(0px, -00px) scale(0.9);
    -webkit-filter: hue-rotate(38deg);
            filter: hue-rotate(38deg);
  }
  80% {
    border-radius: 200px 100px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 10px) scale(1);
            transform: translate(0px, 10px) scale(1);
  }
  100% {
    border-radius: 0px 0px;
    opacity: 0;
    -webkit-transform: translate(0px, 0px) scale(1);
            transform: translate(0px, 0px) scale(1);
  }
}
@keyframes back {
  0% {
    border-radius: 0px 0px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 0px) scale(1);
            transform: translate(0px, 0px) scale(1);
  }
  30% {
    border-radius: 10px 10px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 00px) scale(0.92);
            transform: translate(0px, 00px) scale(0.92);
  }
  40% {
    border-radius: 10px 200px;
    opacity: 0.21;
    -webkit-filter: hue-rotate(-68deg);
            filter: hue-rotate(-68deg);
    -webkit-transform: translate(10px, 10px) scale(1);
            transform: translate(10px, 10px) scale(1);
  }
  60% {
    border-radius: 10px 10px;
    -webkit-transform: translate(5px, -10px) scale(1);
            transform: translate(5px, -10px) scale(1);
    opacity: 0.61;
  }
  70% {
    border-radius: 10px 10px;
    opacity: 0.21;
    -webkit-transform: translate(0px, -00px) scale(0.9);
            transform: translate(0px, -00px) scale(0.9);
    -webkit-filter: hue-rotate(38deg);
            filter: hue-rotate(38deg);
  }
  80% {
    border-radius: 200px 100px;
    opacity: 0.21;
    -webkit-transform: translate(0px, 10px) scale(1);
            transform: translate(0px, 10px) scale(1);
  }
  100% {
    border-radius: 0px 0px;
    opacity: 0;
    -webkit-transform: translate(0px, 0px) scale(1);
            transform: translate(0px, 0px) scale(1);
  }
}

/* Background end */

